.small {
  font-size: 14px;
  line-height: 18px;
}
.body {
  font-size: 16px;
  line-height: 24px;
}
.h4 {
  font-size: 18px;
  line-height: 26px;
}
.h3 {
  font-size: 22px;
  line-height: 30px;
}
.h2 {
  font-size: 24px;
  line-height: 32px;
}
.h1 {
  font-size: 28px;
  line-height: 36px;
}
.weight-300 {
  font-weight: 300;
}
.weight-400 {
  font-weight: 400;
}
.weight-500 {
  font-weight: 500;
}
.weight-600 {
  font-weight: 600;
}
.weight-700 {
  font-weight: 700;
}
.italic {
  font-style: italic;
}
.capitalize {
  text-transform: capitalize;
}
.uppercase {
  text-transform: uppercase;
}
.font-gray {
  color: #ACADB4;
}
.font-blue {
  color: #00C7FF;
}
.background-stripes {
  background-color: #f6f7f8;
  background: repeating-linear-gradient(45deg, #f6f7f8, #f6f7f8 3px, #e2e3e4 3px, #e2e3e4 6px);
  background-repeat: no-repeat;
  background-attachment: fixed;
}
.btn {
  outline: none !important;
  padding: 8px 12px;
  font-size: 16px;
  font-weight: 500;
  border-radius: 4px;
  background-color: #CED0D6;
  border: none;
  cursor: pointer;
  transition: background-color 200ms ease, transform 200ms ease;
}
.btn:hover {
  background-color: #eaebed;
}
.btn:active {
  transform: translateY(1px) scale(0.98);
  transition-duration: 0ms;
  box-shadow: none;
}
.btn[disabled] {
  opacity: 0.5;
  transition: none !important;
  transform: none !important;
  cursor: not-allowed;
}
.btn[disabled]:hover,
.btn[disabled]:active {
  opacity: 0.5;
  transition: none !important;
  transform: none !important;
  background-color: #CED0D6;
  border: none;
  cursor: not-allowed;
}
.btn-ghost {
  background-color: #222429;
  border: 1px solid #00C7FF;
  color: #00C7FF;
  font-weight: 500;
  transition: background-color 200ms ease, transform 200ms ease;
}
.btn-ghost:hover {
  background-color: #000000;
}
.btn-ghost[disabled]:hover,
.btn-ghost[disabled]:active {
  opacity: 0.5;
  transition: none !important;
  transform: none !important;
  background-color: #222429;
  border: 1px solid #00C7FF;
  cursor: not-allowed;
}
.link-btn {
  font-family: 'Roboto', Helvetica Neue, sans-serif;
  font-size: 16px;
  line-height: 24px;
  border: none;
  background-color: transparent;
  color: #00b3e6;
  font-weight: 500;
  padding: 4px 8px;
  outline: none !important;
  cursor: pointer;
  transition: color 200ms ease, background-color 200ms ease;
}
.link-btn:hover {
  text-decoration: underline;
}
.link-btn:active {
  color: #008bb3;
  text-decoration: none;
}
.input {
  margin-right: 8px;
  padding: 9px 11px;
  font-size: 16px;
  border-radius: 6px;
  border: none;
  color: #dedede;
  background-color: #000;
}
.input:focus {
  outline: none;
  box-shadow: 0 0 0 1px #00C7FF;
}
.input[disabled] {
  opacity: 0.5;
  transition: none !important;
  transform: none !important;
  cursor: not-allowed;
}
.result-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
}
.result-bg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #000;
  opacity: 0.9;
}
.result__close {
  position: fixed;
  top: 16px;
  right: 16px;
  z-index: 1;
}
.result-body {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 0 32px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  overflow: auto;
}
.result-content {
  max-width: 960px;
  min-height: 100vh;
  color: #fff;
  flex-grow: 1;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 64px 24px;
}
.result__category {
  padding-bottom: 8px;
  border-bottom: 1px solid #00C7FF;
  margin: 40px 0 32px;
}
.result__group-title {
  margin-bottom: 12px;
  font-size: 22px;
  line-height: 30px;
  color: #00C7FF;
  font-weight: 400 !important;
}
.results__points-line-item {
  display: inline-bock;
  margin-left: 16px;
  color: #fff;
  font-weight: 300;
  font-style: italic;
}
.result__concepts {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 32px;
}
.result__linkages {
  display: flex;
  flex-direction: column;
  margin-bottom: 32px;
  align-items: flex-start;
}
.result__concept {
  background-color: rgba(255, 255, 255, 0.2);
  color: #fff;
  padding: 4px 8px;
  border-radius: 12px;
  margin-right: 12px;
  margin-bottom: 12px;
}
.result__linkage {
  background-color: rgba(255, 255, 255, 0.2);
  color: #fff;
  padding: 6px 8px;
  border-radius: 12px;
  margin-right: 12px;
  margin-bottom: 12px;
  display: flex;
}
.result__linkage-sign {
  display: flex;
  align-items: center;
  margin: -8px 8px;
  padding: 0 6px;
  background-color: rgba(0, 0, 0, 0.4);
  font-style: italic;
}
.result__linkage-sign > span {
  margin: 0 4px;
  display: inline-block;
}
